@import url("https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap");

*,
body {
  padding: 0;
  margin: 0;
  border-radius: 0;
  font-family: "Share Tech Mono", monospace;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}*/

body {
  width: 100vw;
  height: auto;
  overflow-x: hidden;
}

a {
  color: inherit;
  text-decoration: none;
}

a.button {
  display: block;
  background-color: #ffffff;
  border: 2px solid black;
  color: black;
  text-decoration: none;
  padding: 15px 10px;
  position: relative;
  width: 100px;
  height: 15px;
  text-align: center;
  transition: background-color 0.15s;
}

a.button:hover {
  background-color: #ccc;
}

img {
  border-radius: 50%;
}

main {
  width: 100%;
  height: auto;
}

#hero {
  background-image: url("../public/images/mountains.webp");
  background-size: cover;
  background-position: bottom;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 10%;
}

#hero > div {
  height: 95%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  gap: 30px;
}

#hero #hook {
  transform: translateY(-50%);
  top: 50%;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

@media screen and (max-width: 650px) {
  #hero #hook {
    align-items: start;
    padding-left: 15px;
  }
}

.chevron-parent {
  width: 100vw;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.chevron:hover {
  cursor: pointer;
}

.chevron::before {
  border-style: solid;
  border-width: 3px 3px 0 0;
  content: "";
  display: inline-block;
  height: 20px;
  left: 0.15em;
  top: 0.15em;
  transform: rotate(135deg);
  vertical-align: top;
  width: 20px;
  margin: 10px;
  animation: slide-down 1s;
  animation-iteration-count: infinite;
  color: black;
}

@keyframes slide-down {
  0% {
    transform: translateY(0px) rotate(135deg);
  }

  50% {
    transform: translateY(5px) rotate(135deg);
  }

  100% {
    transform: translateY(0px) rotate(135deg);
  }
}

section {
  display: flex;
  width: 80%;
  padding: 100px 10% 100px;
  height: auto;
  border-top: 3px solid black;
}

section:nth-child(even) {
  background-color: #1a6c94;
}

section:nth-child(odd) {
  background-color: #4e89ac;
}

section > div {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

section:nth-child(even) > div:nth-child(2),
section:nth-child(odd) > div:first-child {
  align-items: start;
}

section h1 {
  text-decoration: underline;
}

/*#plugins*/
#plugins > div img {
  margin-top: 10px;
  height: 180px;
  aspect-ratio: 1/1;
  background-color: white;
  border-radius: 50%;
}

section li {
  list-style: none;
  font-size: 1.3em;
  margin-top: 20px;
  line-height: 1.3em;
}

section li:before {
  content: "";
  margin-right: 5px;
  border-left: 4px solid black;
  transition: all 0.25s;
}

#plugins li:nth-child(3):hover::before,
#plugins li:last-child:hover:before,
#plugins li:nth-child(3):active::before,
#plugins li:last-child:active::before {
  margin-right: 3px;
  border-left: 6px solid black;
}

#plugins li:nth-child(3):hover,
#plugins li:last-child:hover {
  cursor: pointer;
}

#plugins li:hover span,
#plugins li:active span {
  color: black !important;
}

#about > div:first-child ul,
#about > div:first-child h1 {
  padding-left: 15%;
}

#about > div img {
  margin-top: 10px;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid black;
}

#contact {
  justify-content: center;
}

#contact > div {
  width: auto;
}

@media screen and (max-width: 515px) {
  section:nth-of-type(odd) {
    flex-direction: column-reverse;
  }
  section:nth-of-type(even) {
    flex-direction: column;
  }
  section {
    align-items: center;
    gap: 30px;
  }
  section > div {
    width: 100%;
  }
  section > div:first-child > ul,
  section > div:first-child > h1 {
    padding-left: 0 !important;
  }
}
