header {
  height: auto;
  width: 100%;
  max-width: 100vw;
  height: 75px;
  position: fixed;
  background: transparent;
  z-index: 1;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

nav p {
  font-size: 35px;
  padding-left: 15px;
  line-height: 75px;
}
@media screen and (max-width: 426px) {
  nav p {
    font-size: 30px;
  }
  @media screen and (max-width: 385px) {
    nav p {
      font-size: 25px;
    }
    @media screen and (max-width: 345px) {
      nav p {
        font-size: 22px;
      }
      @media screen and (max-width: 319px) {
        nav p {
          font-size: 20px;
        }
        @media screen and (max-width: 305px) {
          nav p {
            font-size: 19px;
          }
        }
      }
    }
  }
}

nav a {
  text-decoration: none;
  display: inline-block;
  height: 100%;
}

nav ul {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  column-gap: 10px;
  padding-right: 30px;
}

div#menu-underlay {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.7);
  width: 100vw;
  height: 100vh;
}

nav ul li {
  height: 100%;
}

nav ul a {
  text-decoration: underline;
  font-size: 18px;
  padding: 0 10px;
  line-height: 75px;
}

#hamburger {
  position: relative;
  width: 50px;
  height: 35px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#hamburger input {
  cursor: pointer;
  -webkit-opacity: 0;
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

#hamburger div:nth-of-type(1),
#hamburger div:nth-of-type(2),
#hamburger div:nth-of-type(3) {
  content: "";
  display: block;
  background-color: black;
  width: 50px;
  height: 4px;
  border-radius: 5px;
  transform: translateY(0.5px);
  transition: transform 0.5s, width 0.5s;
}

#hamburger div:nth-of-type(1) {
  transform: translateY(-11px);
}

#hamburger div:nth-of-type(3) {
  transform: translateY(11px);
}

@media screen and (min-width: 651px) {
  div#hamburger {
    display: none;
  }
}

@media screen and (max-width: 650px) {
  div#hamburger {
    display: flex;
    padding: 10px 15px 10px 10px;
    margin: 20px 0 20px 20px;
  }

  ul#menu {
    position: fixed;
    flex-direction: column;
    height: 150px;
    top: 75px;
    right: 0;
    align-items: end;
    justify-content: space-between;
    padding-right: 20px;
  }

  div#menu-underlay {
    z-index: -1;
  }

  ul#menu li {
    height: 40px;
  }

  ul#menu li a {
    text-align: right;
    line-height: 40px;
  }
}

#hamburger input:checked ~ div:nth-of-type(1) {
  transform: rotate(90deg);
}

#hamburger input:checked ~ div:nth-of-type(2) {
  transform: translate(-10px, 9px) rotate(45deg);
  width: 30px;
}

#hamburger input:checked ~ div:nth-of-type(3) {
  transform: translate(10px, 5px) rotate(-45deg);
  width: 30px;
}
